import React, { useEffect, useState } from 'react'
import { NavLink } from 'react-router-dom'
import axios from 'axios'
import { useNavigate } from "react-router-dom";
import { useAlert } from 'react-alert'
import { useDispatch, useSelector } from 'react-redux'
import { login, clearErrors } from '../Services/Actions/userActions'

const Login = () => {
    let navigate = useNavigate();
    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');

    // const alert = useAlert();
    const dispatch = useDispatch();

    const { isAuthenticated, error, loading } = useSelector(state => state.auth);
    const redirect = navigate.search ? navigate.search.split('=')[1] : '/dashboard'
    useEffect(() => {

        if (isAuthenticated) {
            navigate('/dashboard')
        }

        if (error) {
            alert(error);
            dispatch(clearErrors());
        }

    }, [dispatch, isAuthenticated, error, navigate])

    const submitHandler = (e) => {
        e.preventDefault();
        dispatch(login(email, password))
    }
    return (
        <div>
            <div className="container shadow my-5">
                <div className="row">
                    <div className="col-lg-5 d-flex flex-column p-lg-0 p-md-5 p-sm-5 p-5
                 align-items-center text-white text-center justify-content-center form">
                  
                        <h1 className="display-6 fw-bolder mt-sm-5">Conctact For Credentials</h1>
                        {/* <p className="lead text-center">Enter Your Credentials To Login</p> */}
                        <h5 className="mb-4">Press</h5>
                        <NavLink to="/contact" className="btn btn-outline-light rounded-pill pb-2 w-50">Contact</NavLink>
                    </div>
                    <div className="col-lg-7 p-5">
                        <h1 className="display-6 fw-bolder mb-5">
                            <form  onSubmit={submitHandler}>
                                <div className="mb-3">
                                    <label htmlFor="email_field" className="form-label">Email address</label>
                                    <input type="email" className="form-control"  aria-describedby="emailHelp"
                                         id="email_field"
                                         value={email}
                                         onChange={(e) => setEmail(e.target.value)}/>
                                    <div id="emailHelp" className="form-text">We'll never share your email with anyone else.</div>
                                </div>
                                <div className="mb-3">
                                    <label htmlFor="exampleInputPassword1" className="form-label">Password</label>
                                    <input type="password" className="form-control" 
                                        id="password_field"
                                        value={password}
                                        onChange={(e) => setPassword(e.target.value)} />
                                </div>
                                <div className="mb-3 form-check">
                                    <input type="checkbox" className="form-check-input" id="exampleCheck1" />
                                    <label className="form-check-label" htmlFor="exampleCheck1">Remember me</label>
                                </div>
                                <button type="submit" className="btn-theme w-100 mt-4 rounded-pill"
                                id="login_button"
                                >Login</button>
                            </form>
                        </h1>
                    </div>

                </div>
            </div>


        </div>
        //     <div className='container'>
        //     <div className='row'>
        //       <div className='col'>
        //       <div className='login'>
        //     {console.log("User", user)}
        //     <h1>Login</h1>
        //     <input type="text" name="email" value={user.email} onChange={handleChange} placeholder='Enter your Email'></input>
        //     <input type="password" name="password" value={user.password} onChange={handleChange} placeholder='Enter your Password'></input>
        //     <div className='button' onClick={login}>Login</div>
        //     <div>or</div>
        //     {/* <div className='button' onClick={handleClick}>Register</div> */}
        //   </div>
        //       </div>
        //     </div>
        //   </div>
    )
}

export default Login
