    ADMIN_REACT_CODE_REQUEST,
    ADMIN_REACT_CODE_SUCCESS,
    ADMIN_REACT_CODE_FAIL,
    NEW_REACT_CODE_REQUEST,
    NEW_REACT_CODE_SUCCESS,
    NEW_REACT_CODE_FAIL,
    DELETE_REACT_CODE_REQUEST,
    DELETE_REACT_CODE_SUCCESS,
    DELETE_REACT_CODE_FAIL,
    UPDATE_REACT_CODE_REQUEST,
    UPDATE_REACT_CODE_SUCCESS,
    UPDATE_REACT_CODE_FAIL,
    REACT_CODE_DETAILS_REQUEST,
    REACT_CODE_DETAILS_SUCCESS,
    REACT_CODE_DETAILS_FAIL,
    NEW_REVIEW_REQUEST,
    NEW_REVIEW_SUCCESS,
    NEW_REVIEW_FAIL,
    GET_REVIEWS_REQUEST,
    GET_REVIEWS_SUCCESS,
    GET_REVIEWS_FAIL,
    DELETE_REVIEW_REQUEST,
    DELETE_REVIEW_SUCCESS,
    DELETE_REVIEW_RESET,
    DELETE_REVIEW_FAIL,
