import React from "react";

import { Button, Card, Form } from 'react-bootstrap';



function Todo({ todo, index, markTodo, removeTodo }) {
  return (
    <div
      className="todo"
      
    >
      <span style={{ textDecoration: todo.isDone ? "line-through" : "" }}>{todo.text}</span>
      <div>
        <Button variant="outline-success" onClick={() => markTodo(index)}>✓</Button>{' '}
        <Button variant="outline-danger" onClick={() => removeTodo(index)}>✕</Button>
      </div>
    </div>
  );
}

function FormTodo({ addTodo }) {
  const [value, setValue] = React.useState("");

  const handleSubmit = e => {
    e.preventDefault();
    if (!value) return;
    addTodo(value);
    setValue("");
  };

  return (
    <Form onSubmit={handleSubmit} > 
    <Form.Group >
      <Form.Label ><b> Add New Mission</b></Form.Label>
      <Form.Control type="text" className="input" value={value} onChange={e => setValue(e.target.value)} placeholder="What is your misson?" />
    </Form.Group>
    <Form.Group className="mt-3 d-flex justify-content-end">
    {/* <button type="submit" className="btn-theme " onClick={tipSubmit}>Submit<i className="fa fa-arrow-right ms-2"></i></button> */}
    <Button variant="primary mb-3" className="mt-3 btn-theme" type="submit">
      Submit<i className="fa fa-arrow-right ms-2"></i>
    </Button>
    </Form.Group>
  </Form>
  );
}

function WeeklyTask() {
  const [todos, setTodos] = React.useState([
    {
      text: "This is a sampe todo",
      isDone: false
    }
  ]);

  const addTodo = text => {
    const newTodos = [...todos, { text }];
    setTodos(newTodos);
  };

  const markTodo = index => {
    const newTodos = [...todos];
    newTodos[index].isDone = true;
    setTodos(newTodos);
  };

  const removeTodo = index => {
    const newTodos = [...todos];
    newTodos.splice(index, 1);
    setTodos(newTodos);
  };

  return (
    <div className="app">
      <div className="container">
        <h1 className="text-center my-4">Weekly Mission</h1>
        <FormTodo  addTodo={addTodo} />
        <div className="mb-5">
          {todos.map((todo, index) => (
            <Card className="my-2">
              <Card.Body >
                <Todo 
                key={index}
                index={index}
                todo={todo}
                markTodo={markTodo}
                removeTodo={removeTodo}
                />
              </Card.Body>
            </Card>
          ))}
        </div>
      </div>
    </div>
  );
}

export default WeeklyTask;