import React from 'react'
import emailjs from '@emailjs/browser';
import 'aos/dist/aos.css';
import Aos from 'aos';
import { useEffect } from 'react';
//npm i emailjs-com --save
const Contact = () => {
    useEffect(() => {
        Aos.init({ duration: 2000 });
    }, [])
    function sendEmail(e) {
        e.preventDefault();
        emailjs.sendForm('service_wpy6i0b', 'zaryabkhan864', e.target, 'G5CpT9x6KU1BBhTdw')
            .then((result) => {
                console.log(result.text);
                alert('Email has been send...')
                
            }, (error) => {
                console.log(error.text);
            });
        e.target.reset()
    }
    return (
        <div>
            <section id="contact">
                <div className="container my-5 py-5 ">
                    <div className="row mb-5">
                        <div className="col-12">
                            <h3 className="fs-5 text-center mb-0">Contact Us</h3>
                            <h1 className="display-6 text-center mb-4">Have Some <b>Question?</b></h1>
                            <hr className="w-25 mx-auto" />
                        </div>

                    </div>
                    <div className="row">
                        <div className="col-md-6 col-lg-4">
                        <div className="right aos-init " data-aos="flip-up" data-aos-duration="10" data-aos-easing="linear" data-aos-anchor-placement="top-bottom">
                            <img src="/assets/b3.png" alt="About"
                                className="w-100" />
                        </div>
                        </div>
                        <div className="col-md-6 col-lg-8">
                            <form onSubmit={sendEmail}>
                            <div className="right aos-init " data-aos="fade-left" data-aos-duration="10" data-aos-easing="linear" data-aos-anchor-placement="top-bottom">
                                <div className="mb-3">
                                    <label htmlFor="name" className="form-label">Your Name</label>
                                    <input type="text" className="form-control" id="name" placeholder="Muhammad Zaryab Khan" name="name" />
                                </div>
                                </div>
                                <div className="right aos-init " data-aos="fade-left" data-aos-duration="10" data-aos-easing="linear" data-aos-anchor-placement="top-bottom">
                                <div className="mb-3">
                                    <label htmlFor="email" className="form-label">Email address</label>
                                    <input type="email" className="form-control" id="exampleFormControlInput1" placeholder="name@example.com" name="email" />
                                </div>
                                </div>
                                <div className="right aos-init " data-aos="fade-left" data-aos-duration="10" data-aos-easing="linear" data-aos-anchor-placement="top-bottom">
                                <div className="mb-3">
                                    <label htmlFor="exampleFormControlTextarea1" className="form-label">Your Message</label>
                                    <textarea className="form-control" id="Message" rows="5" name="message"></textarea>
                                </div>
                                </div>
                                <button type="submit" className="btn-theme">Send Message <i className="fa fa-paper-plane ms-2"></i></button>
                            </form>
                        </div>
                    </div>
                </div>
            </section>
        </div>
    )
}

export default Contact
