    ALL_NODE_CODE_REQUEST,
    ALL_NODE_CODE_SUCCESS,
    ALL_NODE_CODE_FAIL ,
    ADMIN_NODE_CODE_REQUEST,
    ADMIN_NODE_CODE_SUCCESS,
    ADMIN_NODE_CODE_FAIL,
    NEW_NODE_CODE_REQUEST,
    NEW_NODE_CODE_SUCCESS,
    NEW_NODE_CODE_FAIL,
    DELETE_NODE_CODE_REQUEST,
    DELETE_NODE_CODE_SUCCESS,
    DELETE_NODE_CODE_FAIL,
    UPDATE_NODE_CODE_REQUEST,
    UPDATE_NODE_CODE_SUCCESS,
    UPDATE_NODE_CODE_FAIL,
    NODE_CODE_DETAILS_REQUEST,
    NODE_CODE_DETAILS_SUCCESS,
    NODE_CODE_DETAILS_FAIL,
    NEW_REVIEW_REQUEST,
    NEW_REVIEW_SUCCESS,
    NEW_REVIEW_FAIL,
    GET_REVIEWS_REQUEST,
    GET_REVIEWS_SUCCESS,
    GET_REVIEWS_FAIL,
    DELETE_REVIEW_REQUEST,
    DELETE_REVIEW_SUCCESS,
    DELETE_REVIEW_RESET,
    DELETE_REVIEW_FAIL,
    CLEAR_ERRORS,

    ALL_NODE_ERROR_REQUEST,
    ALL_NODE_ERROR_SUCCESS,
    ALL_NODE_ERROR_FAIL,
    DELETE_NODE_ERROR_REQUEST,
    DELETE_NODE_ERROR_SUCCESS,
    DELETE_NODE_ERROR_FAIL,

