import React, { useEffect } from 'react'
import { useSelector } from 'react-redux';
import { Navigate, Route, Routes, useNavigate } from 'react-router-dom';

import MainLayout from './MainLayout';

import Login from './Login';

import Dashboard from './Dashboard';
import Node from './Node';

import ASP from './ASP';
import Home from './Home';
import About from './About';
import Project from './Project';
import Skills from './Skills';
import Contact from './Contact';
import ReactFile from './React/ReactFile';
import Search from './Search/Search';
import NodeFile from './Node/NodeFile';
import WeeklyTask from './WeeklyTask/WeeklyTask';
import JavaScriptFile from './JavaScript/JavaScriptFile';

const AuthRoute = () => {
  const navigate = useNavigate()
  const { isAuthenticated } = useSelector(state => state.auth);


  useEffect(() => {
    if (localStorage?.getItem('TOKEN')) navigate('dashboard')
  }, [])


  const CheckAuthetication = ({ children }) => {
    // alert(authenticated);
    if (localStorage?.getItem('TOKEN')) {
      return children
    } else {
      return <Navigate to='home' replace />
    }
  }
  return (
    <>
      <Routes>
        <Route index path='/home' element={<Home />} />
        <Route path="/about" element={<About />} />
        <Route path="/project" element={<Project />} />
        <Route path="/skills" element={<Skills />} />
        <Route path="/contact" element={<Contact />} />

        <Route exact path="/login" element={<Login />} />
        <Route render={({ history }) => <Search history={history} />} />
        <Route path='/' element={
          <CheckAuthetication>
            <MainLayout />
          </CheckAuthetication>
        }>
          <Route path='dashboard' element={<Dashboard />} />
          <Route path='search/:keyword' element={<ReactFile />} />
          <Route path='weeklytask' element={<WeeklyTask />} />
          {/* <Route path='node' element={<Node />} /> */}
          <Route path='asp' element={<ASP />} />
          <Route path='react' element={<ReactFile />} />
          <Route path='node' element={<NodeFile />} />
          <Route path='javaScript' element={<JavaScriptFile  />} />
        </Route>
        <Route path='*' element={<div><p>ERROR PAGE</p></div>} />
      </Routes>
    </>
  )
}

export default AuthRoute