export const nodecodesReducer = (state = { nodecode: [] }, action) => {
    switch (action.type) {
    case ALL_NODE_CODE_REQUEST:
        return{
            loading:true,
            nodecodes:[]
        }
      case ALL_NODE_CODE_SUCCESS:
          return {
              loading: false,
              nodecodes: action.payload.nodecodes,
              nodecodesCount: action.payload.nodecodesCount,
              resPerPage: action.payload.resPerPage
          }
          case ALL_NODE_CODE_FAIL:
              return{
                  loading:false,
                  error:action.payload
              }
              case CLEAR_ERRORS:
                  return{
                      ...state,
                      error: null
                  }

        default:
            return state;
    }
}
