export const newProductReducer = (state = { product: {} }, action) => {
    switch (action.type) {

        case NEW_NODE_CODE_REQUEST:
            return {
                ...state,
                loading: true
            }

        case NEW_NODE_CODE_SUCCESS :
            return {
                loading: false,
                success: action.payload.success,
                product: action.payload.product
            }

        case NEW_NODE_CODE_FAIL:
            return {
                ...state,
                error: action.payload
            }

        case NEW_NODE_CODE_RESET:
            return {
                ...state,
                success: false
            }

        case CLEAR_ERRORS:
            return {
                ...state,
                error: null
            }

        default:
            return state
    }
}
//for delete and update
export const nodecodeReducer = (state = {}, action) => {
    switch (action.type) {

        case DELETE_NODE_CODE_REQUEST :
        case UPDATE_NODE_CODE_REQUEST:
            return {
                ...state,
                loading: true
            }

        case DELETE_NODE_CODE_SUCCESS:
            return {
                ...state,
                loading: false,
                isDeleted: action.payload
            }

        case UPDATE_NODE_CODE_SUCCESS:
            return {
                ...state,
                loading: false,
                isUpdated: action.payload
            }


        case DELETE_NODE_CODE_FAIL:
        case UPDATE_NODE_CODE_FAIL:
            return {
                ...state,
                error: action.payload
            }

        case DELETE_NODE_CODE_RESET:
            return {
                ...state,
                isDeleted: false
            }

        case UPDATE_NODE_CODE_RESET:
            return {
                ...state,
                isUpdated: false
            }

        case CLEAR_ERRORS:
            return {
                ...state,
                error: null
            }

        default:
            return state
    }
}

