
import './App.css';
import Navbar from './compoents/Navbar';
import Home from './compoents/Home';
import About from './compoents/About';
import Skills from './compoents/Skills';
import Contact from './compoents/Contact';
import Footers from './compoents/Footers';
import { Routes, Route } from 'react-router-dom';
import Login from './compoents/Login';
import Register from './compoents/Register';
import Dashboard from './compoents/Dashboard';
import Project from './compoents/Project';
import ProtectedRoute from './compoents/ProtectedRoute';
import { useEffect, useState } from 'react';
import { BrowserRouter } from 'react-router-dom';
import { Provider } from 'react-redux'
import store from '../src/Services/Store/store'
import AuthRoute from './compoents/AuthRoute';
import { positions, transitions, Provider as AlertProvider } from 'react-alert';
import AlertTemplate from 'react-alert-template-basic';
// Import css files
import "react-alice-carousel/lib/alice-carousel.css";
// import "react-alice-carousel/lib/scss/alice-carousel.scss";
const options = {
  timeout: 5000,
  position: positions.BOTTOM_CENTER,
  transition: transitions.SCALE
}

function App() {
  const [auth, setauth] = useState(false);
  const [auth1, setauth1] = useState(true);
  const isLoggedIn = async () => {
    try {
      const res = await fetch('/auth', {
        method: "GET",
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json"
        },
        creadentials: "include"
      })
      if (res.status === 200) {
        setauth(true)
        setauth1(false)
      }
      if (res.status === 401) {
        setauth(false)
        setauth1(true)
      }
    } catch (error) {
      console.log(error)
    }
  }
  useEffect(() => {
    isLoggedIn()
  }, []);
  return (
    <>
      <Provider store={store}>
        <AlertProvider template={AlertTemplate} {...options}>
        <BrowserRouter>
          <Navbar />
          <AuthRoute />
          <Footers />
        </BrowserRouter>
        </AlertProvider>
        
      </Provider>
    </>
  );
}

export default App;