import { Navigate, NavLink, useNavigate } from "react-router-dom";
import { FaBars, FaHome, FaUser ,FaNodeJs,FaReact} from "react-icons/fa";

import { SiCsharp} from "react-icons/si";
import { MdMessage } from "react-icons/md";
import { BiAnalyse, BiSearch } from "react-icons/bi";
import { BiCog ,BiLogOut} from "react-icons/bi";
import { DiJavascript } from "react-icons/di";
import { BsCartCheck } from "react-icons/bs";
import { useState } from "react";
import axios from "axios";
// import { motion } from "framer-motion";
const routes = [
  {
    path: "/dashboard",
    name: "Dashboard",
    icon: <FaHome />,
  },
  {
    path: "/javaScript",
    name: "JavaScript",
    icon: <DiJavascript />,
  },
  {
    path: "/node",
    name: "Nodejs",
    icon: <FaNodeJs />,
  },
  {
    path: "/react",
    name: "React",
    icon: <FaReact />,
  },
  // {
  //   path: "/file-manager",
  //   name: "File Manager",
  //   icon: <AiTwotoneFileExclamation />,
  // },
  // {
  //   path: "/order",
  //   name: "Order",
  //   icon: <BsCartCheck />,
  // },
  // {
  //   path: "/saved",
  //   name: "Saved",
  //   icon: <AiFillHeart />,
  // },
  // {
  //   path: "/settings",
  //   name: "Settings",
  //   icon: <BiCog />,
  // },
];

const SideBar = ({ children }) => {
  const navigate = useNavigate()
  const [isOpen, setIsOpen] = useState(false);
  const toggle = () => setIsOpen(!isOpen);
  const logout=()=>{
    axios.get('/api/v1/logout')
    .then(()=>{
     localStorage.clear() 
     navigate('home')
    }
    )
  }
  const inputAnimation = {
    hidden: {
      width: 0,
      padding: 0,
      transition: {
        duration: 0.2,
      },
    },
    show: {
      width: "140px",
      padding: "5px 15px",
      transition: {
        duration: 0.2,
      },
    },
  };

  const showAnimation = {
    hidden: {
      width: 0,
      opacity: 0,
      transition: {
        duration: 0.5,
      },
    },
    show: {
      opacity: 1,
      width: "auto",
      transition: {
        duration: 0.5,
      },
    },
  };

  return (
    <>
      <div className="main-container">
        <div
          style={{
            width: isOpen ? "200px" : "45px",
          }}
          className={`sidebar `}
        >
          <div className="top_section">
            {/* <AnimatePresence>
              {isOpen && (
                <motion.h1
                  variants={showAnimation}
                  initial="hidden"
                  animate="show"
                  exit="hidden"
                  className="logo"
                >
                  DoSomeCoding
                </motion.h1>
              )}
            </AnimatePresence> */}

            <div className="bars">
              <FaBars onClick={toggle} />
            </div>
          </div>
          {/* <div className="search">
            <div className="search_icon">
              <BiSearch />
            </div>
            <AnimatePresence>
              {isOpen && (
                <motion.input
                  initial="hidden"
                  animate="show"
                  exit="hidden"
                  variants={inputAnimation}
                  type="text"
                  placeholder="Search"
                />
              )}
            </AnimatePresence>
          </div> */}
          <section className="routes">
       
            {routes.map((route, index) => {
              return (
                <NavLink
                  to={route.path}
                  key={index}
                  className="link"
                  activeclassname="side-active"
                >
                  <div className="icon">{route.icon}</div>
                  {/* <AnimatePresence>
                    {isOpen && (
                      <motion.div
                        variants={showAnimation}
                        initial="hidden"
                        animate="show"
                        exit="hidden"
                        className="link_text"
                      > */}
                        {isOpen && route.name}
                      {/* </motion.div>
                    )}
                  </AnimatePresence>
                */}
                </NavLink> 
              );
            })}
            
           
          </section>
          <div className="top_section">
            {/* <AnimatePresence>
              {isOpen && (
                <motion.h1
                  variants={showAnimation}
                  initial="hidden"
                  animate="show"
                  exit="hidden"
                  className="logo"
                >
                  DoSomeCoding
                </motion.h1>
              )}
            </AnimatePresence> */}

            <div className="bars">
              <BiLogOut onClick={logout} />
            </div>
          </div>
        </div>

        <main>{children}</main>
      </div>
    </>
  );
};

export default SideBar;
