import { useState } from "react";
import JavaScriptCode from "../JavaScript/code/JavaScriptCode";
import JavaScriptCrud from "../JavaScript/CRUD/JavaScriptCrud";
import JavaScriptErrors from "../JavaScript/errors/JavaScriptErrors";
import JavaScriptProjects from "../JavaScript/projects/JavaScriptProjects";
import JavaScriptTips from "../JavaScript/tips/JavaScriptTips";



function JavaScriptTabs() {
    const [toggleState, setToggleState] = useState(1);

    const toggleTab = (index) => {
        setToggleState(index);
    };

    return (
        < div className="container-fluid px-0">
            <div className="row">
                <div className="col-12">
                    <div className="bloc-tabs">
                    <button
                            className={toggleState === 1 ? "tabs active-tabs" : "tabs"}
                            onClick={() => toggleTab(1)}
                        >
                            Projects
                        </button>
                        <button
                            className={toggleState === 2 ? "tabs active-tabs" : "tabs"}
                            onClick={() => toggleTab(2)}
                        >
                            Code
                        </button>
                        <button
                            className={toggleState === 3 ? "tabs active-tabs" : "tabs"}
                            onClick={() => toggleTab(3)}
                        >
                            Errors
                        </button>
                        <button
                            className={toggleState === 4 ? "tabs active-tabs" : "tabs"}
                            onClick={() => toggleTab(4)}
                        >
                            Tips
                        </button>
                        <button
                            className={toggleState === 5 ? "tabs active-tabs" : "tabs"}
                            onClick={() => toggleTab(5)}
                        >
                            Enter Code / Erros / Tips
                        </button>
                    </div>
                </div>
                <div className="col-12">
                    <div className="content-tabs">
                    <div
                            className={toggleState === 1 ? "content  active-content" : "content"}
                        >
                            <JavaScriptProjects />
                        </div>
                        <div
                            className={toggleState === 2 ? "content  active-content" : "content"}
                        >
                            <JavaScriptCode />
                        </div>

                        <div
                            className={toggleState === 3 ? "content  active-content" : "content"}
                        >
                            <JavaScriptErrors />
                        </div>

                        <div
                            className={toggleState === 4 ? "content  active-content" : "content"}
                        >
                            <JavaScriptTips />
                        </div>
                        <div
                            className={toggleState === 5 ? "content  active-content" : "content"}
                        >
                            <JavaScriptCrud />
                        </div>
                    </div>
                </div>
            </div>



        </div>
    );
}

export default JavaScriptTabs;