import React, { useState } from 'react'
import axios from "axios"
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import ReactQuill from 'react-quill'; // ES6

import 'react-quill/dist/quill.snow.css';

const ReactCrud = () => {
    // for code
    const [getCode, setCode] = useState({
        codeDescription: "",
        code: ""
    })







    // for error
    const [getError, setError] = useState({
        errorDescription: "",
        solution: ""
    })
 





    //for tips
    const [getTip, setTip] = useState({
        tipDescription: "",
        tip: ""
    })



    // const handleChangeCode = e => {
    //     let { name, value } = e.target
    //     setCode({ ...getCode, [name]: value })
    // }
    function handleChangeCode(name, value) {
        setCode({ ...getCode, [name]: value })
    }



    function handleChangeError(name, value) {
        setError({ ...getError, [name]: value })
    }



    function handleChangeTip(name, value) {
        setTip({ ...getTip, [name]: value })
    }



    // for submit code 
    const codeSubmit = () => {
        const { codeDescription, code } = getCode
        console.log('this is all code', getCode)
        if (codeDescription && code) {
            axios.post("/api/v1/admin/reactcode/new", getCode)
                .then(() => {
                    toast.success('🦄 Code Added SuccessFully!', {
                        position: "bottom-right",
                        autoClose: 2000,
                        hideProgressBar: false,
                        closeOnClick: true,
                        pauseOnHover: true,
                        draggable: true,
                        progress: undefined,
                    });
                })
                .catch(() => {
                    toast.error('Sorry Only Admin Has Submit Rights!', {
                        position: "bottom-right",
                        autoClose: 2000,
                        hideProgressBar: false,
                        closeOnClick: true,
                        pauseOnHover: true,
                        draggable: true,
                        progress: undefined,
                    });
                })

        }
        else {
            alert("Invalid inputs")
        }
    }



    const errorSubmit = () => {
        const { errorDescription, solution } = getError

        if (errorDescription && solution) {
            axios.post("/api/v1/admin/reacterror/new", getError)
                .then(() => {
                    toast.success('🦄 Error Added SuccessFully!', {
                        position: "bottom-right",
                        autoClose: 2000,
                        hideProgressBar: false,
                        closeOnClick: true,
                        pauseOnHover: true,
                        draggable: true,
                        progress: undefined,
                    });
                })
                .catch(() => {
                    toast.error('Sorry Only Admin Has Submit Rights!', {
                        position: "bottom-right",
                        autoClose: 2000,
                        hideProgressBar: false,
                        closeOnClick: true,
                        pauseOnHover: true,
                        draggable: true,
                        progress: undefined,
                    });
                })

        }
        else {
            alert("Invalid inputs")
        }
    }
    const tipSubmit = () => {
        const { tipDescription, tip } = getTip

        if (tipDescription && tip) {
            axios.post("/api/v1/admin/reacttip/new", getTip)
                .then(() => {
                    toast.success('🦄 Tip Added SuccessFully!', {
                        position: "bottom-right",
                        autoClose: 2000,
                        hideProgressBar: false,
                        closeOnClick: true,
                        pauseOnHover: true,
                        draggable: true,
                        progress: undefined,
                    });
                })
                .catch(() => {
                    toast.error('Sorry Only Admin Has Submit Rights!', {
                        position: "bottom-right",
                        autoClose: 2000,
                        hideProgressBar: false,
                        closeOnClick: true,
                        pauseOnHover: true,
                        draggable: true,
                        progress: undefined,
                    });
                })

        }
        else {
            alert("Invalid inputs")
        }
    }




    return (
        <div className='container-fluid'>
            <div className="row d-flex justify-content-center">
                <div className="col-12">
                    <h1>Enter Your New Code</h1>
                </div>
                <div className="col-6">
                    <input type="text" className="form-control w-100" placeholder="Enter Code Description" aria-label="Example text with button addon" aria-describedby="button-addon1"
                        name="codeDescription" value={getCode.codeDescription} onChange={(e) => { handleChangeCode('codeDescription', e.target.value) }} />
                </div>
                <div className="col-6">
                    <ReactQuill
                        placeholder='write something amazing'
                        modules={Error.modules}
                        formats={Error.formats}
                        name="code"
                        id="comment"
                        // value={getCode.code}
                        onChange={(value) => { handleChangeCode('code', value) }}
                        rows="5" />


                </div>
                <div className="col-12 text-end mt-5">
                    <button type="submit" className="btn-theme " onClick={codeSubmit}>Submit<i className="fa fa-arrow-right ms-2"></i></button>
                </div>

                <ToastContainer />
            </div>




            <div className="row d-flex justify-content-center">
                <div className="col-12">
                    <h1>Enter Your New Error</h1>
                </div>
                <div className="col-6">
                    <input type="text" className="form-control w-100" placeholder="Enter Error Description" aria-label="Example text with button addon" aria-describedby="button-addon1"
                        name="errorDescription" value={getError.errorDescription} onChange={(e) => { handleChangeError('errorDescription', e.target.value) }} />
                </div>
                <div className="col-6">
                    <ReactQuill
                        placeholder='write something amazing'
                        modules={Error.modules}
                        formats={Error.formats}
                        name="code"
                        id="comment"
                        // value={getCode.code}
                        onChange={(value) => { handleChangeError('solution', value) }}
                        rows="5" />


                </div>
                <div className="col-12 text-end mt-5">
                    <button type="submit" className="btn-theme " onClick={errorSubmit}>Submit<i className="fa fa-arrow-right ms-2"></i></button>
                </div>

                <ToastContainer />
            </div>




            <div className="row d-flex justify-content-center">
                <div className="col-12">
                    <h1>Enter Your New Tip</h1>
                </div>
                <div className="col-6">
                    <input type="text" className="form-control w-100" placeholder="Enter Tip Description" aria-label="Example text with button addon" aria-describedby="button-addon1"
                        name="tipDescription" value={getTip.tipDescription} onChange={(e) => { handleChangeTip('tipDescription', e.target.value) }} />
                </div>
                <div className="col-6">
                    <ReactQuill
                        placeholder='write something amazing'
                        modules={Error.modules}
                        formats={Error.formats}
                        name="code"
                        id="comment"
                        // value={getCode.code}
                        onChange={(value) => { handleChangeTip('tip', value) }}
                        rows="5" />


                </div>
                <div className="col-12 text-end mt-5">
                    <button type="submit" className="btn-theme " onClick={tipSubmit}>Submit<i className="fa fa-arrow-right ms-2"></i></button>
                </div>

                <ToastContainer />
            </div>



        </div>
    )
}

Error.modules = {
    toolbar: [
        [{ header: "1" }, { header: "2" }, { header: [3, 4, 5, 6] }, { font: [] }],
        [{ size: [] }],
        ["bold", "italic", "underline", "strike", "blockquote"],
        [{ list: "ordered" }, { list: "bullet" }],
        ["link", "image", "video"],
        ["clean"],
        ["code-block"],
    ]
}
Error.formats = [
    "header",
    "font",
    "size",
    "bold",
    "italic",
    "underline",
    "strike",
    "blockquote",
    "list",
    "bullet",
    "link",
    "image",
    "video",
    "code-block",
];


export default ReactCrud