export const reactcodesReducer = (state = { reactcode: [] }, action) => {
    switch (action.type) {
    case ALL_REACT_CODE_REQUEST:
        return{
            loading:true,
            reactcodes:[]
        }
      case ALL_REACT_CODE_SUCCESS:
          return {
              loading: false,
              reactcodes: action.payload.reactcodes,
              reactcodesCount: action.payload.reactcodesCount,
              resPerPage: action.payload.resPerPage
          }
          case ALL_REACT_CODE_FAIL:
              return{
                  loading:false,
                  error:action.payload
              }
              case CLEAR_ERRORS:
                  return{
                      ...state,
                      error: null
                  }

        default:
            return state;
    }
}
