export const newProductReducer = (state = { product: {} }, action) => {
    switch (action.type) {

        case NEW_REACT_CODE_REQUEST:
            return {
                ...state,
                loading: true
            }

        case NEW_REACT_CODE_SUCCESS :
            return {
                loading: false,
                success: action.payload.success,
                product: action.payload.product
            }

        case NEW_REACT_CODE_FAIL:
            return {
                ...state,
                error: action.payload
            }

        case NEW_REACT_CODE_RESET:
            return {
                ...state,
                success: false
            }

        case CLEAR_ERRORS:
            return {
                ...state,
                error: null
            }

        default:
            return state
    }
}
//for delete and update
export const reactcodeReducer = (state = {}, action) => {
    switch (action.type) {

        case DELETE_REACT_CODE_REQUEST :
        case UPDATE_REACT_CODE_REQUEST:
            return {
                ...state,
                loading: true
            }

        case DELETE_REACT_CODE_SUCCESS:
            return {
                ...state,
                loading: false,
                isDeleted: action.payload
            }

        case UPDATE_REACT_CODE_SUCCESS:
            return {
                ...state,
                loading: false,
                isUpdated: action.payload
            }


        case DELETE_REACT_CODE_FAIL:
        case UPDATE_REACT_CODE_FAIL:
            return {
                ...state,
                error: action.payload
            }

        case DELETE_REACT_CODE_RESET:
            return {
                ...state,
                isDeleted: false
            }

        case UPDATE_REACT_CODE_RESET:
            return {
                ...state,
                isUpdated: false
            }

        case CLEAR_ERRORS:
            return {
                ...state,
                error: null
            }

        default:
            return state
    }
}

