export const javaScriptcodesReducer = (state = { javaScriptcode: [] }, action) => {
    switch (action.type) {
    case ALL_JAVASCRIPT_CODE_REQUEST:
        return{
            loading:true,
            javaScriptcodes:[]
        }
      case ALL_JAVASCRIPT_CODE_SUCCESS:
          return {
              loading: false,
              javaScriptcodes: action.payload.javaScriptcodes,
              javaScriptcodesCount: action.payload.javaScriptcodesCount,
              resPerPage: action.payload.resPerPage
          }
          case ALL_JAVASCRIPT_CODE_FAIL:
              return{
                  loading:false,
                  error:action.payload
              }
              case CLEAR_ERRORS:
                  return{
                      ...state,
                      error: null
                  }

        default:
            return state;
    }
}
