//for delete and update
export const javaScriptcodeReducer = (state = {}, action) => {
    switch (action.type) {

        case DELETE_JAVASCRIPT_CODE_REQUEST :
        case UPDATE_JAVASCRIPT_CODE_REQUEST:
            return {
                ...state,
                loading: true
            }

        case DELETE_JAVASCRIPT_CODE_SUCCESS:
            return {
                ...state,
                loading: false,
                isDeleted: action.payload
            }

        case UPDATE_JAVASCRIPT_CODE_SUCCESS:
            return {
                ...state,
                loading: false,
                isUpdated: action.payload
            }


        case DELETE_JAVASCRIPT_CODE_FAIL:
        case UPDATE_JAVASCRIPT_CODE_FAIL:
            return {
                ...state,
                error: action.payload
            }

        case DELETE_JAVASCRIPT_CODE_RESET:
            return {
                ...state,
                isDeleted: false
            }

        case UPDATE_JAVASCRIPT_CODE_RESET:
            return {
                ...state,
                isUpdated: false
            }

        case CLEAR_ERRORS:
            return {
                ...state,
                error: null
            }

        default:
            return state
    }
}

