import axios from 'axios';

import {
    ALL_JAVASCRIPT_CODE_REQUEST,
    ALL_JAVASCRIPT_CODE_SUCCESS,
    ALL_JAVASCRIPT_CODE_FAIL ,
    ADMIN_JAVASCRIPT_CODE_REQUEST,
    ADMIN_JAVASCRIPT_CODE_SUCCESS,
    ADMIN_JAVASCRIPT_CODE_FAIL,
    NEW_JAVASCRIPT_CODE_REQUEST,
    NEW_JAVASCRIPT_CODE_SUCCESS,
    NEW_JAVASCRIPT_CODE_FAIL,
    DELETE_JAVASCRIPT_CODE_REQUEST,
    DELETE_JAVASCRIPT_CODE_SUCCESS,
    DELETE_JAVASCRIPT_CODE_FAIL,
    UPDATE_JAVASCRIPT_CODE_REQUEST,
    UPDATE_JAVASCRIPT_CODE_SUCCESS,
    UPDATE_JAVASCRIPT_CODE_FAIL,
    JAVASCRIPT_CODE_DETAILS_REQUEST,
    JAVASCRIPT_CODE_DETAILS_SUCCESS,
    JAVASCRIPT_CODE_DETAILS_FAIL,
    NEW_REVIEW_REQUEST,
    NEW_REVIEW_SUCCESS,
    NEW_REVIEW_FAIL,
    GET_REVIEWS_REQUEST,
    GET_REVIEWS_SUCCESS,
    GET_REVIEWS_FAIL,
    DELETE_REVIEW_REQUEST,
    DELETE_REVIEW_SUCCESS,
    DELETE_REVIEW_RESET,
    DELETE_REVIEW_FAIL,
    CLEAR_ERRORS,

    ALL_JAVASCRIPT_ERROR_REQUEST,
    ALL_JAVASCRIPT_ERROR_SUCCESS,
    ALL_JAVASCRIPT_ERROR_FAIL,
    DELETE_JAVASCRIPT_ERROR_REQUEST,
    DELETE_JAVASCRIPT_ERROR_SUCCESS,
    DELETE_JAVASCRIPT_ERROR_FAIL,

    ALL_JAVASCRIPT_TIP_REQUEST,
    ALL_JAVASCRIPT_TIP_SUCCESS,
    ALL_JAVASCRIPT_TIP_FAIL,
    DELETE_JAVASCRIPT_TIP_REQUEST,
    DELETE_JAVASCRIPT_TIP_SUCCESS,
    DELETE_JAVASCRIPT_TIP_FAIL
} from '../Constrants/javaScriptConstrants'

export const getJavaScriptCodes = (keyword = '',currentPage = 1) => async (dispatch) => {
    try {
        dispatch({type:ALL_JAVASCRIPT_CODE_REQUEST})
        const {data} = await axios.get(`/api/v1/javaScriptcode?keyword=${keyword}&page=${currentPage}`)
        dispatch({
            type:ALL_JAVASCRIPT_CODE_SUCCESS,
            payload:data
        })
    } catch (error) {
        dispatch({
            type: ALL_JAVASCRIPT_CODE_FAIL,
            payload: error.response.data.message
        })
    }
}

export const newProduct = (productData) => async (dispatch) => {
    try {

        dispatch({ type: NEW_JAVASCRIPT_CODE_REQUEST })

        const config = {
            headers: {
                'Content-Type': 'application/json'
            }
        }

        const { data } = await axios.post(`/api/v1/admin/product/new`, productData, config)

        dispatch({
            type: NEW_JAVASCRIPT_CODE_SUCCESS,
            payload: data
        })

    } catch (error) {
        dispatch({
            type: NEW_JAVASCRIPT_CODE_FAIL,
            payload: error.response.data.message
        })
    }
}

// Delete javaScriptcode (Admin)
export const deleteJavaScriptCode = (id) => async (dispatch) => {
    try {

        dispatch({ type: DELETE_JAVASCRIPT_CODE_REQUEST })
      
        const { data } = await axios.delete(`/api/v1/admin/javaScriptcode/${id}`)

        dispatch({
            type: DELETE_JAVASCRIPT_CODE_SUCCESS,
            payload: data.success
        })

    } catch (error) {
        dispatch({
            type: DELETE_JAVASCRIPT_CODE_FAIL,
            payload: error.response.data.message
        })
    }
}

// Update Product (ADMIN)
