import { createStore, combineReducers, applyMiddleware } from 'redux';
import thunk from 'redux-thunk';
import { composeWithDevTools } from 'redux-devtools-extension'
import {
    reactcodesReducer, reactcodeReducer, reacterrorReducer, reacterrorsReducer,
    reacttipReducer, reacttipsReducer
} from '../Reducers/reactReducers'
import {
    nodecodesReducer, nodecodeReducer, nodeerrorReducer, nodeerrorsReducer,
    nodetipReducer, nodetipsReducer
} from '../Reducers/nodeReducers'
import {
    javaScriptcodesReducer, javaScriptcodeReducer, javaScripterrorReducer, javaScripterrorsReducer,
    javaScripttipReducer, javaScripttipsReducer
} from '../Reducers/javaScriptReducers'
import { authReducer } from '../Reducers/userReducers'

const reducer = combineReducers({

    reactcode: reactcodeReducer,
    reactcodes: reactcodesReducer,

    reacterror: reacterrorReducer,
    reacterrors: reacterrorsReducer,

    reacttip: reacttipReducer,
    reacttips: reacttipsReducer,

    nodecode: nodecodeReducer,
    nodecodes: nodecodesReducer,

    nodeerror: nodeerrorReducer,
    nodeerrors: nodeerrorsReducer,

    nodetip: nodetipReducer,
    nodetips: nodetipsReducer,

    javaScriptcode: javaScriptcodeReducer,
    javaScriptcodes: javaScriptcodesReducer,

    javaScripterror: javaScripterrorReducer,
    javaScripterrors: javaScripterrorsReducer,

    javaScripttip: javaScripttipReducer,
    javaScripttips: javaScripttipsReducer,


    auth: authReducer
})
let initialState = {}

const middlware = [thunk];
const store = createStore(reducer, initialState, composeWithDevTools(applyMiddleware(...middlware)))

export default store;