import React, { Fragment, useEffect, useState } from 'react'
import Pagination from 'react-js-pagination'
import { useDispatch, useSelector } from 'react-redux';
import { getJavaScriptErrors, deleteJavaScriptError } from '../../../Services/Actions/javaScriptActions'
import { useAlert } from 'react-alert';
import Loader from '../../Loader/Loader';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

import Search from '../../Search/Search';
import ReactQuill from 'react-quill'; // ES6
import axios from 'axios';
const JavaScriptErrors = ({ match }) => {
  const [javaScripterrorcopy, setJavaScriptErrorCopy] = useState()

  const [currentPage, setCurrentPage] = useState(1)
  const alert = useAlert();
  const dispatch = useDispatch();
  const { loading, javaScripterrors, error, javaScripterrorsCount, resPerPage } = useSelector(state => state.javaScripterrors);
  console.log('Here is avaliable stuff javaScripterrors', javaScripterrors)
  const { error: deleteError, isDeleted } = useSelector(state => state.javaScripterror)
  const keyword = match?.params?.keyword
  useEffect(() => {
    if (error) {
      return alert.error(error)
    }
    dispatch(getJavaScriptErrors(keyword, currentPage));
  }, [dispatch, alert, error, keyword, currentPage])

  useEffect(() => {
    setJavaScriptErrorCopy(javaScripterrors)

  }, [javaScripterrors])
  const searchHandler = (data) => {
    // data.preventDefault()
    console.log('babababa', data)
    const filterJavaScriptErrors = javaScripterrors.filter(obj => obj?.errorDescription?.includes(data))
    setJavaScriptErrorCopy(filterJavaScriptErrors)
  }

  function setCurrentPageNo(pageNumber) {
    setCurrentPage(pageNumber)
  }
  const deleteJavaScriptErrorHandler = (id) => {
    dispatch(deleteJavaScriptError(id))
    if (dispatch(deleteJavaScriptError(id)) == true) {
      console.log('Code delete')
      toast.error("Error has been Deleted", {
        position: "bottom-right",
        autoClose: 1000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
      })
      dispatch(javaScripterrors());
    }
    else {
      toast.error("sorry only admin can Delete", {
        position: "bottom-right",
        autoClose: 1000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
      })
    }




  }


  return (
    <Fragment>
      {loading ? <Loader /> : (
        <Fragment>
          <div className="row">
            <div className="col-lg-6">
              <h1>JavaScript Errors</h1>
            </div>
            <div className="col-lg-6">
              {/* <Route render={({ history }) => <Search history={history} />} /> */}
              <Search searchHandler={searchHandler} />
            </div>
            <div className="col-lg-12">
              <div className="accordion accordion-flush" id="accordionFlushExample">
                {javaScripterrorcopy && javaScripterrorcopy.map((e, index) => (
                  <div className="accordion-item" key={index}>
                    <h2 className="accordion-header" id={`flush-headingOne-${index}`}>
                      <button className="accordion-button collapsed" type="button" data-bs-toggle="collapse"
                        data-bs-target={`#flush-collapseOne-${index}`} aria-expanded="false" aria-controls={`#flush-collapseOne-${index}`}>
                        {e.errorDescription}
                      </button>
                    </h2>
                    <div id={`flush-collapseOne-${index}`} className="accordion-collapse collapse"
                      aria-labelledby={`flush-headingOne-${index}`} data-bs-parent="#accordionFlushExample">
                      <div className="accordion-body">
                        {/* <code>{e.code}</code>  */}
                        <ReactQuill
                          placeholder='write something amazing'
                          name="error"
                          id="comment"
                          value={e.solution}
                          // onChange={(value)=>{handleChangeCode('code',value)}}
                          rows="5" />
                      </div>
                      <div className='text-center my-3'><button className='btn btn-danger'
                        onClick={() => deleteJavaScriptErrorHandler(e._id)}>Delete</button></div>
                    </div>

                  </div>
                ))}
              </div>

            </div>
            {resPerPage <= javaScripterrorsCount && (
              <div className='d-flex justify-content-center mt-5'>
                <Pagination
                  activePage={currentPage}
                  itemsCountPerPage={resPerPage}
                  totalItemsCount={javaScripterrorsCount}
                  onChange={setCurrentPageNo}
                  nextPagetext={'Next'}
                  prevPageText={'Prev'}
                  firstPageText={'First'}
                  lastPageText={'Last'}
                  itemClass="page-item"
                  linkClass="page-link"
                />

              </div>
            )}
          </div>
        </Fragment>
      )}

    </Fragment>

  )
}
Error.modules = {
  toolbar: [
    [{ header: "1" }, { header: "2" }, { header: [3, 4, 5, 6] }, { font: [] }],
    [{ size: [] }],
    ["bold", "italic", "underline", "strike", "blockquote"],
    [{ list: "ordered" }, { list: "bullet" }],
    ["link", "image", "video"],
    ["clean"],
    ["code-block"],
  ]
}
Error.formats = [
  "header",
  "font",
  "size",
  "bold",
  "italic",
  "underline",
  "strike",
  "blockquote",
  "list",
  "bullet",
  "link",
  "image",
  "video",
  "code-block",
];

export default JavaScriptErrors