import React from 'react'
import { Outlet } from 'react-router-dom'
import SideBar from './Sidebar'

const MainLayout = () => {
  return (
    <div style={{display:'flex',flexDirection:'row'}}>
        <SideBar /> 
        <div className='container-fluid px-0'>
            <Outlet />
        </div>
    </div>
  )
}

export default MainLayout